<template>
	<view :class="{body:models=='1'||models=='', bodyNight:models=='0'}">
		<!-- 返回按钮 -->
		<image class="returnIcon" src="../../static/return_white.png" @click="returnLastpage"></image>
		<!-- 模拟导航栏文字 -->
		<view class="tabarText">我的积分</view>
		<!-- 背景图 -->
		<image v-if="models=='0'" class="rankBg" src="../../static/index/rankListBg_night.png"></image>
		<image v-else class="rankBg" src="../../static/index/rankListBg.png"></image>
		<!-- 主体内容 -->
		<view class="content">
			<!-- 我的排名 -->
			<view class="mineRank">
				<view class="rank">
					<view class="h6">我的排名</view>
					<view class="h5">{{data.my_ranking.rownum}}</view>
				</view>
				<view class="info">
					<img :src="data.my_ranking.avatar" />
					<input class="motto" type="text" v-model="content" placeholder="点击输入你的座右铭" @confirm="setMotto">
					<view class="h6">{{data.my_ranking.nickname}}</view>
				</view>
				<view class="sentence">
					<view class="h6">刷题数量</view>
					<view class="h5">{{data.my_ranking.num}}<view>题</view></view>
				</view>
			</view>
			<!-- 排行榜 -->
			<view class="rankList">
				<view class="userList" v-for="item in data.ranking" :key="item.index">
					<view class="rank">{{item.rownum}}</view>
					<view class="userInfo">
						<view class="userHead">
							<image class="avartl" :src="item.avatar || '/static/index/user_head.png'"></image>
							<img class="icons" v-if="item.rownum==1" src="/static/index/champion.png">
							<img class="icons" v-if="item.rownum==2" src="/static/index/runnerUp.png">
							<img class="icons" v-if="item.rownum==3" src="/static/index/thirdPlace.png">
						</view>
						<view class="info">
							<view class="name">{{item.nickname || '匿名用户'}}</view>
							<view class="motto">{{item.content || "暂无座右铭"}}</view>
							<view class="icon">
								<view class="keepDays">坚持{{item.insist_day_num}}天</view>
							</view>
						</view>
					</view>
					<view class="userSentence">
						<view>刷题数量</view>
						<view class="userSent">{{item.num}}</view>
					</view>
				</view>
			</view>
		</view>
		<!-- 加载动画 -->
		<view class="loadModul" v-if="show">
			<u-loading-icon></u-loading-icon>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { rank, motto } from '@/common/http.api.js';
	export default {
		data() {
			return {
				//自定义数据
				data:{},
				show:false, //是否显示加载动画
				content:'',
				models:'1', //白天模式
				token:''
			}
		},
		onLoad() {
			const token = store.state.vuex_token 
			this.token = token
			this.show = true
			this.models = store.state.model
			if(this.models=='0'){
				console.log('进入夜间模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#011733',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}else{
				console.log('进入白天模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#6ab4ff',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}
			rank({token}).then(res => {
				console.log(res)
				this.show = false
				this.data = res.data.data
				this.content = res.data.data.my_ranking.content
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			setMotto() {
				motto({token:this.token,content:this.content}).then(res => {
					console.log(res)
					rank({token:this.token}).then(res => {
						console.log(res)
						this.data = res.data.data
						this.content = res.data.data.my_ranking.content
					}).catch(err=>{
						console.log(err)
					})
				}).catch(err=>{
					console.log(err)
				})
			},
			// 返回上级页面
			returnLastpage(){
				uni.navigateBack({
					delta:1
				})
			},
		}
	}
</script>

<style lang="less">
	.loadModul{
		position: fixed;
		z-index: 999;
		background-color: rgba(192, 192, 192, .8);
		border-radius: 20rpx;
		width: 300rpx;
		height: 250rpx;
		left: 50%;
		right: 50%;
		top:50%;
		bottom: 50%;
		transform: translate(-50%,-50%);
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.returnIcon{
		width: 50rpx;
		height: 50rpx;
		position: fixed;
		top: 6%;
		z-index: 1;
		left: 50rpx;
	}
	.tabarText{
		top: 6%;
		left: 42%;
		position: fixed;
		color: #fff;
		font-size: 32rpx;
	}
	.rankBg{
		width: 100%;
		height: 500rpx;
	}
	.content{
		width: 90%;
		margin: 0 5%;
		background-color: #fff;
		top: 350rpx;
		border-radius: 20rpx;
		position: absolute;
		.mineRank{
			display: flex;
			text-align: center;
			justify-content: space-between;
			margin: 40rpx;
			.h6{
				font-weight: 500;
				font-size: 36rpx;
			}
			.h5{
				width: 120rpx;
				white-space: normal;
				overflow-x: scroll;
				font-size: 54rpx;
				font-weight: 500;
			}
			.rank{
				flex: 3;
				.h5{
					color: #ffd656;
				}
			}
			.info{
				flex: 6;
				margin-top: -20rpx;
				.motto{
					font-size: 24rpx;
					color: #666;
				}
				img{
					width: 120rpx;
					height: 120rpx;
					border-radius: 50%;
				}
				.h6{
					font-size: 32rpx;
				}
			}
			.sentence{
				flex: 3;
				.h5{
					color: #ffd656;
					display: inline-block;
					view{
						font-size: 24rpx;
						display: inline-block;
					}
				}
			}
		}
		.userList{
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin: 30rpx;
			.rank{
				flex: 1;
				margin-right: 20rpx;
			}
			.userInfo{
				flex: 7;
				display: flex;
				.userHead{
					padding: 20rpx;
					padding-left: 0;
					position: relative;
					.avartl{
						width: 100rpx;
						height: 100rpx;
						border-radius: 50%;
					}
					.icons{
						width: 40rpx;
						height: 40rpx;
						position: absolute;
						right: 8rpx;
						top: -6rpx;
					}
				}
				.info{
					width: 320rpx;
					.motto{
						color: #666;
						margin-bottom: 10rpx;
						overflow-x: scroll;
						width: 88%;
						white-space: nowrap;
						font-size: 24rpx;
					}
					.icon{
						overflow: hidden;
						.keepDays{
							float: left;
							color: white;
							font-size: 24rpx;
							background-color: #9ed5ff;
							border-radius: 50rpx;
							margin-right: 10rpx;
							padding: 4rpx 14rpx;
						}
						.interl{
							color: white;
							font-size: 24rpx;
							background-color: #ffd656;
							border-radius: 50rpx;
							float: left;
							padding: 4rpx 14rpx;
						}
					}
				}
			}
			.userSentence{
				flex: 4;
				text-align: center;
				font-size: 32rpx;
				line-height: 70rpx;
				.userSent{
					color: #ffd656;
					font-size: 48rpx;
					width: 140rpx;
					overflow-x: scroll;
					white-space: nowrap;
				}
				.userSent::after{
					content: '题';
					font-size: 20rpx;
					display: inline-block;
				}
			}
		}
	}
	.body{
		position: relative;
		background-color: #f7f7fb;
		min-height: 100vh;
		overflow: auto;
	}
	.bodyNight{
			position: relative;
			background-color: #3b3b3b;
			min-height: 100vh;
			overflow: auto;
			.content{
				background-color: #303030;
				color: #fff;
			}
		}

</style>